export const validate = (inputs) => {
  const errors = {};
  const emailReg = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i

  const { firstname, lastname, city, address, email, phone } = inputs
  const { cardnumber, exp, cvv } = inputs

  if (!firstname) {
    errors.firstname = '  '
  } else if (firstname.length < 2) {
    errors.firstname = '  '
  }

  if (!lastname) {
    errors.lastname ='  '
  } else if (lastname.length < 2) {
    errors.lastname = '  '
  }


  if (!city) {
    errors.city = '  '
  } else if (city.length < 2) {
    errors.city = '  '
  }

  if (!address) {
    errors.address = '  '
  } else if (address.length < 2) {
    errors.address = '  '
  }

  if (!email) {
    errors.email = '   '
  } else if (!emailReg.test(email)) {
    errors.email = '   '
  }

  if (!phone) {
    errors.phone = '   '
  } else if (phone.length <= 9) {
    errors.phone = '   '
  }


  if(!cardnumber || cardnumber.length < 16) errors.cardnumber = '   '
  if(!exp || exp.length < 4) errors.exp = '    '
  if(!cvv || cvv.length < 4) errors.cvv = '  '

  return errors
};